<template>
  <div class="farm-card">
    <el-card :class="'box-card-' + (is_online ? 'online' : 'offline')" shadow="hover">
      <template #header>
        <div class="card-header">
          <span class="card-title">{{title}}</span>
          <el-tag type="info" class="card-header-tag">{{workers.length}} worker{{plurial_worker}}</el-tag>
        </div>
      </template>
      <el-row>
        <el-col :span="12">
          <span>{{total_power}}W <span class="mdi mdi-flash"></span></span>
        </el-col>
        <el-col :span="12">
          <span>{{total_khs}}kh/s <span class="mdi mdi-pickaxe"></span></span>
        </el-col>
      </el-row>
    </el-card>
  </div>
</template>

<script>
export default {
  name: "FarmCard",
  props: {
    title: String,
    workers: Array
  },
  methods: {
    reducing_acc: (prev, curr) => {
      return prev + curr
    }
  },
  computed: {
    plurial_worker: function () {
      return this.workers.length <= 1 ? '' : 's'
    },
    total_khs: function () {
      return this.workers.length > 0 ? this.workers.map((value) => value.stats?.total_khs | 0).reduce(this.reducing_acc) : 0
    },
    total_power: function () {
      return this.workers.length > 0 ? this.workers.map((value) => value.stats?.power.reduce(this.reducing_acc) | 0).reduce(this.reducing_acc) : 0
    },
    is_online: function () {
      return this.total_power > 0
    }
  }
}
</script>

<style scoped>
.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.card-header > .card-title {
  font-weight: bold;
  font-size: 1.7rem;
}

.farm-card {
  cursor: pointer;
}

.mdi-flash {
  color: yellowgreen;
}

.mdi-pickaxe {
  color: blue;
}

.box-card-online {
  border-top-color: green;
}

.box-card-offline {
  border-top-color: red;
}
</style>