import axios from "axios";

const headers = {
    'Content-Type': 'application/json'
}

const api = {
    get_farms: async () => {
        return await axios.get('http://localhost:80/farm')
    },
    get_farm: async (farm_id) => {
        return await axios.get('http://localhost:80/farm/' + farm_id)
    },
    post_farm: async({name}) => {
        return await axios.post('http://localhost:80/farm/', {
            name: name,
            workers: []
        }, {headers: headers})
    },
    post_worker: async (farm_id) => {
        return await axios.post('http://localhost:80/worker', {
            farm_id: farm_id
        }, {headers: headers})
    }
}

export default api